.container {
  justify-content: center;
  align-items: center;
  height: 680px;
  display: flex;
}

.timer {
  color: #eee;
  -webkit-user-select: none;
  user-select: none;
  background: #222;
  border-radius: 12px;
  gap: 20px;
  width: max-content;
  padding: 20px 30px;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
  box-shadow: 0 4px 15px #00000080;
}

.field {
  flex-direction: column;
  align-items: center;
  min-width: 50px;
  display: flex;
}

.value {
  letter-spacing: 1px;
  text-align: center;
  background: linear-gradient(135deg, #6a5af9, #8e4fff);
  border-radius: 10px;
  min-width: 48px;
  padding: 10px 20px;
  font-size: 2.5rem;
  font-weight: 700;
  box-shadow: 0 0 15px #7b5fff88;
}

.label {
  color: #bbb;
  text-transform: uppercase;
  letter-spacing: .1em;
  margin-top: 8px;
  font-size: .85rem;
}
